// core
import React, { memo, useCallback } from 'react'

import classnames from 'classnames'

// components
import { IComponentProps } from 'components'

// utils
import { runCallback } from 'utils'

// styles
import css from './Icon.module.scss'

export type IconColor =
  | 'primary'
  | 'white'
  | 'light'
  | 'blue'
  | 'neutral'
  | 'success'
  | 'warning'
  | 'danger'
  | 'txt'

export type IconSize = 'mini' | 'small' | 'medium' | 'large' | 'extraLarge' | string

export interface IIconProps extends IComponentProps {
  title?: string
  /**
   * Name from Material Icons
   */
  name: string
  /**
   * Icon color
   */
  color?: IconColor
  /**
   * Size of icon
   */
  size?: IconSize
  /**
   * On click event
   */
  onClick?: () => void
}

export const Icon = memo(function Icon({
  className,
  name,
  title,
  color,
  size = 'medium',
  onClick,
}: IIconProps) {
  const handleClick = useCallback(() => runCallback(onClick), [onClick])

  return (
    <i
      title={title}
      className={classnames(
        'material-icons',
        css.root,
        color ? css[color] : '',
        css[size],
        className
      )}
      onClick={handleClick}>
      {name}
    </i>
  )
})
