import React from "react";
import cx from 'classnames';
import { IComponentProps, Icon, showToastOk } from "components";
import { useStore } from "stores";
import css from '../Sessions.module.scss'
import { Dropdown } from "components/basic/Dropdown/Dropdown";
import { SwingSession } from "gen";
import { useHistory } from "react-router-dom";
import { observer } from "mobx-react";
import { useTranslation } from "react-i18next";

interface IActionCellProps extends IComponentProps{
    bSession?: boolean,
    app:string,
    session:SwingSession,
}

export const ActionCell = observer(({bSession=false,app, session, className,classes}:IActionCellProps) => {
    const history = useHistory();
    const {permissionsStore, sessionsStore } = useStore();
    const {t} = useTranslation();
    const kill = (session:SwingSession, app:string,force?:boolean) => {
        if(force) {
            sessionsStore.forceKill(session, app)
        } else {
            sessionsStore.kill(session,app)
        }
        history.goBack()
    }

    const view = (session: SwingSession) => {
        history.push('/session' + session.applicationPath +'/'+ session.id)
        sessionsStore.setActiveTab('view');
    }

    const logs = (session: SwingSession) => {
        history.push('/session' + session.applicationPath +'/'+ session.id)
        sessionsStore.setActiveTab('logs');
    }

    const copyID = () => {
        if(session.id) {
            navigator.clipboard.writeText(session.id);
            showToastOk(t('copyInstIDMsg'));
        }
    }
    
    return (
        <Dropdown
            className={cx(css.actionCellDropdown,className)}
            buttons={
                bSession ?
                [
                    {label: t('sessions.threadDump'), icon:'list', onClick:()=>sessionsStore.requestThreadDump(session, app, bSession)},
                    {shouldShow: permissionsStore.get('startRecording') && session.canRecord, label: t('sessions.record'), icon:'fiber_manual_record', onClick:()=>sessionsStore.startRecording(session, app, bSession)},
                    {label: t('sessions.copyInstID'), icon:'content_copy', onClick:()=>copyID()},
                    {
                        label: t('sessions.shutdown'),
                        icon:'power_settings_new', 
                        onClick:()=>kill(session, app), 
                        buttons:[{label: t('sessions.forceKill'), icon:'power_settings_new', onClick:()=>kill(session, app,true)}]
                    },
                ]
                :
                [
                    {label: t('sessions.view'), icon:'view_quilt', onClick:()=>view(session)},
                    {label: t('sessions.threadDump'), icon:'list', onClick:()=>sessionsStore.requestThreadDump(session, app)},
                    {shouldShow: permissionsStore.get('startRecording') && session.canRecord, label: t('sessions.record'), icon:'fiber_manual_record', onClick:()=>sessionsStore.startRecording(session, app)},
                    {label: t('sessions.copyInstID'), icon:'content_copy', onClick:()=>copyID()},
                    {shouldShow: permissionsStore.get('logsView') && session.loggingEnabled, label: t('routes.logs'), icon:'list', onClick:()=>logs(session)},
                    {
                        label: t('sessions.shutdown'), 
                        icon:'power_settings_new', 
                        color:'danger',
                        onClick:()=>sessionsStore.kill(session,app), 
                        buttons:[{label: t('sessions.forceKill'), icon:'power_settings_new', onClick:()=>sessionsStore.forceKill(session, app)}]
                    },
                ]
            }
        >
            <Icon name={'more_vert'} />
        </Dropdown>
    )
})