import React from 'react';
import css from '../LeftSideMenu.module.scss'
import { observer } from 'mobx-react';
import cx from 'classnames';
import { useTranslation } from 'react-i18next';
import { useStore } from 'stores';

const Footer = observer((props: { center?: boolean }) => {
  const { globalStore } = useStore()
  const { t } = useTranslation()
  return(
    <>
    {
      globalStore.customization?.footerHTML! ? 
      <div className={cx(css.footer, props.center && css.center)} dangerouslySetInnerHTML={{__html:globalStore.customization?.footerHTML!}}></div>
      :
      <>
        {t('general.developed')} <a href="https://www.webswing.org">webswing.org</a><br />
        <a target='_blank' href='https://www.webswing.org/docs/'>{t('routes.documentation')}</a>, <a target='_blank' href='https://portal.webswing.org/support'>{t('routes.reportbug')}</a>
        <div className={cx(css.footer, props.center && css.center)} >
          {(new Date()).getFullYear().toString()} Webswing Ltd. {t('general.allRightReserved')}
        </div>
      </>
    }
  </>
)});

export default Footer;
